<template>
	<div id="store-yucunjin">
		<public-header title="预存金明细"></public-header>
		<div class="content">
			<div class="wrap-search">
				<div class="tab-group flex-lr">
					<div class="tab-item" :class="{active: filter.items.trade_type == 0}" @click="onToggleStatus(0)">全部</div>
					<div class="tab-item" :class="{active: filter.items.trade_type == 1}" @click="onToggleStatus(1)">订单扣除</div>
					<div class="tab-item" :class="{active: filter.items.trade_type == 2}" @click="onToggleStatus(2)">充值</div>
					<div class="tab-item" :class="{active: filter.items.trade_type == 3}" @click="onToggleStatus(3)">其他变动</div>
				</div>
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
				<van-list
						v-model="records.loading"
						:finished="records.finished"
						@load="onLoad"
						:offset="100"
						:immediate-check="false"
						v-if="!records.empty"
						class="current-list"
				>
					<div class="current-item" v-for="(item, index) in records.data" :key="item.id">
						<div class=" padding-10" :class="{'border-1px-t': index > 0 }">
							<div class="current-detail font-14 padding-10-b">
								<span class="font-bold c-default">{{item.trade_type_cn}}</span>
								<span class="font-bold c-primary" v-if="item.fee > 0">+{{item.fee}}</span>
								<span class="font-bold c-danger" v-else>{{item.fee}}</span>
							</div>
							<div class="current-detail font-14">
								<span class="c-gray">{{item.addtime_cn}}</span>
								<span class="c-gray">余额：{{Lang.dollarSign}}{{item.yucunjin}}</span>
							</div>
						</div>
					</div>
					<div class="loaded" v-if="records.finished">
						<div class="loaded-tips">没有更多了</div>
					</div>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/order_no_con.png" alt="">
					<p>没有符合条件的数据!</p>
				</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				showPreLoading:true,
				isRefresh: false,
				filter: {
					items: {
						trade_type: '0'
					}
				}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'errander/yucunjin/list',
					data: {}
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			},
			onToggleStatus(value){
				value = parseInt(value);
				if(this.filter.items.trade_type == value) {
					return;
				}
				this.filter.items.trade_type = value;
			}
		},
		mounted(){
			this.onLoad();
		},

		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			},
		}
	}
</script>

<style>
	#store-yucunjin .wrap-search~.van-pull-refresh{
		margin-top: 45px;
		border-top: 1px solid #f5f5f5;
	}
	#store-yucunjin .content .current-item{
		background-color: #fff;
	}
	#store-yucunjin .content .current-item:first-child{
		border-top: 1px solid #e0e0e0;
	}
	#store-yucunjin .content .current-item .current-detail {
		display: flex;
		flex-wrap: nowrap;
		justify-content: space-between;
		align-items: center;
	}
</style>


